import React from "react";
import SectionContainer from "../../../StyledComponents/HomeStyle/SectionContainer";
import UserCp from "./UserCp";
import styled from "styled-components";
import NoUserCp from "./NoUserCp";
import axios from "axios";
import { useQuery } from "@tanstack/react-query";

const CheckUserCp = () => {
  const getLoginCheckInfo = async () => {
    try {
      return await axios.get("/page/login-check");
    } catch (error) {
      console.error(error);
    }
  };

  const { data: loginCheck, isLoading } = useQuery({
    queryKey: ["loginCheck"],
    queryFn: getLoginCheckInfo,
  });

  if (isLoading) console.log("loginCheck is Loading");

  return (
    <SectionContainer3 width={"320px"}>
      <section>{loginCheck?.data ? <UserCp /> : <NoUserCp />}</section>
    </SectionContainer3>
  );
};

export default CheckUserCp;

export const SectionContainer3 = styled(SectionContainer)`
  position: absolute;
  right: 0;
  z-index: 1;
  width: ${(props) => props.width};
`;
